import { Component } from 'react';
import $ from "jquery";

class Menu extends Component{
    constructor(props) {
        super(props);
    }
    displayMenu = () => {
        $('.displaymenutext').css({left: "-1200px"})
        $('.displaymenutext').show().animate({left: "0px"}, 1000);
        document.getElementsByClassName('close')[0].style.display = "block";
        for (var i = 0; i < document.getElementsByClassName('menublock').length; i++) {
        document.getElementsByClassName('menublock')[i].style.display = "block";
        }
    }
    displayContact = () => {
        $('.displaymenutext').css({left: "-1200px"})
        $('.displaymenutext').show().animate({left: "0px"}, 1000);
        document.getElementsByClassName('contactblock')[0].style.display = "block";
        document.getElementsByClassName('close')[0].style.display = "block";
    }
    render() {
        if ( this.props.class === "menu" ) {
        return(
            <button onClick={this.displayMenu} className={this.props.class}>{this.props.title}</button>
        )
        } else {
            return (
                <button onClick={this.displayContact} className={this.props.class}>{this.props.title}</button>
            )
        }
    }
}

export default Menu;