import React from 'react';

function Contact(props){


    return(
        <div className="contactblock">
            <div className="info">{props.info}</div>
            <p className="contact-details address">{props.address}</p> 
            <p className="contact-details reservations">{props.reservations}</p> 
            <form id="contactform" action="#">
            <input className="cf" type="text" placeholder="Name" />
            <input className="cf" type="email" placeholder="Email" />
            <input className="cf" type="date" placeholder="Date" />
            <textarea rows="8" name="comment" placeholder="Message" ></textarea>
            <button className="sendmsg" onClick={(e) => e.preventDefault()}>Send Message</button>
            </form>
        </div>
    )
}

export default Contact