import React from 'react';
import $ from "jquery";


function Close() {
    
    const close = () => {
        $('.displaymenutext').animate({left: "-1500px"}, 1000);
        $('.cartmenu').animate({left: "-1500px"}, 1000);
        $('.menublock').hide(1000);
        $('.contactblock').hide(1000);
    }

    return (
        <span className="close" onClick={close}>&#8592;</span>
    ) 
}

export default Close