import React, { useState } from 'react';
import $ from 'jquery';

function Cart() {
    function display(){
        $('.cartmenu').css({left: "-1200px", display: "grid"});
        $('.cartmenu').animate({left: "0px"}, 1000);
        document.getElementsByClassName('close')[0].style.display = "block";
       }
 
    return (
        <div className="cartblock" onClick={display}>
            <i className="fas fa-shopping-cart"></i>
            <div className="circle"></div>
        </div>
    )
}



export default Cart