import React from 'react';
import Close from './Close'

function Cartmenu() {

    return(
        <div className="cartmenu">
          <Close />
          <div className="carthead">
            <ul>
              <li>Food</li>
              <li>Qty</li>
              <li>Price</li>
              <li>Total</li>
              
            </ul>
          </div>
          <div className="ullist">
            
          </div>
          
          <div id="total">
            <hr />
            <h5>Total</h5>
          </div>
        </div>
    )
}

export default Cartmenu